@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html {
  overflow: hidden;
  height: 100%;
}
body {
  color: #333;
  margin: 0;
  height: 100%;
  overflow: auto;
}

a {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.header, .footer {
  display: flex;
  flex-direction: column;
  z-index: 2;
  margin-bottom: 100px;
}

.header h1, .header h3, .header p, .footer h2, .footer p {
  text-align: center;
}

.header h2, .footer h2 {
  margin-bottom: 0;
}

.header p, .footer p {
  margin-bottom: 30px;
}


.footer {
  margin-top: 100px;
}

.headerImage {
  max-width: 990px;
  margin: auto;
}

.background {
  background-image: url(/static/media/bg3.382f1e1d.png);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  transition: opacity 1s ease-in-out;
  width: 100%;
  z-index: -1;
}
h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
}

h1 {
  margin-top: 100px;
  font-size: 70px;
  margin-bottom: 0;
}

h3 {
  font-size: 32px;
}

p {
  font-family: 'Source Sans Pro', serif;
  font-size: 18px;
}

.spacing {
  height: 200px;
}

.spacer {
  height: 60px;
}

.carouselContainer {
  padding: 0 12vw;
}

ul {
  display: flex;
}

ul li {
  margin-right: 34px;
  list-style: none;
}

ul li:nth-child(odd) {
  -webkit-transform: translateY(1rem);
          transform: translateY(1rem);
}

ul li:nth-child(even) {
  -webkit-transform: translateY(-1rem);
          transform: translateY(-1rem);
}

.locationContainer, .addressContainer {
  display: flex;
  flex-direction: row;
  align-items: center ;
}

.addressContainer {
  align-items: flex-start;
}

.locationContainer img, .addressContainer img {
  margin-right: 13px;
}

.locationContainer p, .addressContainer p {
  margin: 0 0 5px 5px;
}

@media only screen and (max-width: 1024px) {
  .whiteBg {
    right: 20rem !important;
  }
}
@media only screen and (max-width: 768px) {
  .contentWrapper {
    flex-direction: column;
  }
  .singleContent {
    order: 2;
    padding: 0 !important;
    margin-bottom: 30px;
  }
  .imageContainer {
    order: 1;
  }
  .whiteBg {
    right: 0 !important;
  }
  .headerImage, footerImage {
    width: 80%;
  }
  .imageContainer {
    -webkit-transform: translateY(-1.4rem) !important;
            transform: translateY(-1.4rem) !important;
  }
}


